import React, { Component } from 'react';
import './App.css';
import mapboxgl from 'mapbox-gl';
import scrollama from 'scrollama';
import isMobile from 'ismobilejs'
import Minimap from './vendor/mapboxgl-minimap'

import { Legend } from './components/Legend'
import { ToogleMapInteractivity } from './components/ToogleMapInteractivity'

const alignments = {
    'left': 'lefty',
    'center': 'centered',
    'right': 'righty'
}

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentChapter: props.chapters[0],
            device: isMobile().phone ? 'mobile' : 'desktop',
            interactiveMap: false
        };
        //this.setState = this.setState.bind(this);
    }

    componentDidMount() {
        const config = this.props;
        const device = this.state.device
        const mapStart = config.chapters[0].location[device];

        mapboxgl.accessToken = config.accessToken;

        const map = new mapboxgl.Map({
            container: this.mapContainer,
            style: config.style,
            center: mapStart.center,
            zoom: mapStart.zoom,
            pitch: mapStart.pitch,
            bearing: mapStart.bearing,
            attributionControl: false,
            //interactive: false
        });

        const marker = new mapboxgl.Marker();
        if (config.showMarkers) {
            marker.setLngLat(mapStart[device].center).addTo(map);
        }

        const addOverlay = (layerOpt) => {
            //console.log(map.getSource('layerOpt.layer.ids '))

            let layers = map.getStyle().layers;
            // Find the index of the first symbol layer in the map style
            let firstSymbolId;
            for (var i = 0; i < layers.length; i++) {
                if (layers[i].type === 'symbol') {
                    firstSymbolId = layers[i].id;
                    break;
                }
            }
            // check if source is available in config and is not prev added
            if (layerOpt.source && !map.getSource(layerOpt.layer.id)) {
                map.addSource(layerOpt.layer.id, layerOpt.source)
            }

            if (map.getLayer(layerOpt.layer.id)) {
                map.setLayoutProperty(layerOpt.layer.id, 'visibility', 'visible')
            } else {
                map.addLayer(layerOpt.layer, firstSymbolId);
            }

            // console.log(layerOpt.layer, layers)

        }

        const setState = this.setState.bind(this);
        // instantiate the scrollama
        const scroller = scrollama();

        map.on('load', () => {

            // add minimap
            if (config.showMiniMap) {
                map.addControl(new Minimap(config.miniMap), "bottom-right");
            }


            // setup the instance, pass callback functions
            scroller
            .setup({
                step: '.step',
                offset: 0.9,
                progress: true
            })
            .onStepEnter(response => {
                const chapter = config.chapters.find(chap => chap.id === response.element.id);
                setState({ currentChapter: chapter });
                map.flyTo(chapter.location[device]);
                if (config.showMarkers) {
                    marker.setLngLat(chapter.location[device].center);
                }
                const layers = chapter.onChapterEnter.addLayers
                if (layers && layers.length > 0) {
                    layers.forEach(layer => {
                        addOverlay(layer)
                    });
                }
            })
            .onStepExit(response => {
                const chapter = config.chapters.find(chap => chap.id === response.element.id);
                const layerIds = chapter.onChapterExit.removeLayers
                if (layerIds && layerIds.length > 0) {
                    layerIds.forEach(layerId => {
                        map.setLayoutProperty(layerId, 'visibility', 'none')
                        //map.removeLayer(layerId);
                    });
                }
            });
        });

        window.addEventListener('resize', scroller.resize);
    }

    toogleMapInteractivity = () => {
        this.setState({ interactiveMap: !this.state.interactiveMap })
    }

    render() {
        const config = this.props;
        const theme = config.theme;
        const currentChapterID = this.state.currentChapter.id;
        return (
            <div>
                <div 
                    ref={el => this.mapContainer = el} 
                    className="absolute top right left bottom" 
                    id='map' 
                    // toogle interaztive map by chaning z-value
                    style={this.state.interactiveMap ? {zIndex: 10}: {zIndex: -5} }
                />
                <ToogleMapInteractivity
                    clicked={this.toogleMapInteractivity}
                    isActive={this.state.interactiveMap}
                />
                <div id="tooltip" />
                <div id="story" style={this.state.interactiveMap ? {opacity: 0} : {opacity:1}}>
                    {config.title &&
                        <div id="header" className={theme}>
                            <h1>{config.title}</h1>
                            {config.subtitle &&
                                <h2>{config.subtitle}</h2>
                            }
                            {config.byline &&
                                <p>{config.byline}</p>
                            }
                        </div>
                    }

                    <div id="features" className={alignments[config.alignment]}>
                        {
                            config.chapters.map(chapter =>
                                <Chapter key={chapter.id} theme={theme} {...chapter} currentChapterID={currentChapterID} />
                            )
                        }
                    </div>

                    {config.footer &&
                        <div id="footer" className={theme}>
                            <p>{config.footer}</p>
                        </div>
                    }
                </div>
            </div>
        );
    }

}

const Chapter = ({ id, theme, title, image, flourishID, embed, description, header, body, link, currentChapterID, legend }) => {
    const classList = id === currentChapterID ? "step active" : "step";
    return (
        <div id={id} className={classList}>
            <div className={theme}>
                {title &&
                    <h2 className="title">{title}</h2>
                }
                {image &&
                    <img src={image}></img>
                }
                {description &&
                    <p>{description}</p>
                }
                {header &&
                    <h3>{header}</h3>
                }
                {body &&
                    <p>{body}</p>
                }
                {flourishID &&
                    <div className="flourish-embed" data-src={`visualisation/${flourishID}`}></div>
                }
                {embed &&
                    <iframe src={embed} title={id} className="embed" width="100%" height="400" frameBorder="0" />
                }
                {link &&
                    <a href={link.url} target='_blank' rel="noopener noreferrer">{link.title}</a>
                }
                {legend &&
                    <Legend title={legend.title} layout={legend.layout} />
                }
            </div>
        </div>
    )
}

export default App;
